import React, { useState, useEffect } from 'react';

import Icon, {icons, sizes} from '../Icon/Icon';

import CalendarList from './CalendarList';
import CalendarSwitch from './CalendarSwitch';
import styles from './CalendarList.scss';

const Calendar = ({
  handleClientLoad,
  isSignedIn,
  handleAuthClick,
  handleSignoutClick,
  listUpcomingEvents,
  events,
  addEvent,
  updateEvent,
}) => {
  const actualMonth = new Date().getMonth() + 1;
  const [activeMonth, setActiveMonth] = useState(actualMonth);

  useEffect(() => {
    handleClientLoad();
  })

  if (isSignedIn && !events.length) {
    listUpcomingEvents(activeMonth);
  }

  const addLeadingZero = (date) => {
    return ('0'+date).slice(-2)
  }

  const mapOnMonth = (unmappedEvents) => {
    const date = new Date();
    const newDate = new Date(date.getFullYear(), activeMonth, 0);
    const lastDay = newDate.getDate();

    const newMap = [];
    for (let i = 1; i <= lastDay; i++) {
      const searchDate = `${date.getFullYear()}-${addLeadingZero(activeMonth)}-${addLeadingZero(i)}`;
      const filteredEvent = unmappedEvents.filter(event => event.date === `${date.getFullYear()}-${addLeadingZero(activeMonth)}-${addLeadingZero(i)}`)?.[0];
      newMap.push({
        date: searchDate,
        shift: filteredEvent?.shift || '',
        id: filteredEvent?.id || ''
      })
    }
    return newMap;
  }

    
  const chunk = (arr, size) =>
    Array.from({ length: Math.ceil(arr.length / size) }, (v, i) =>
      arr.slice(i * size, i * size + size)
    );

  return (
    <>
      {
        !isSignedIn ?
          <button className={styles.switchButton} onClick={handleClientLoad}>
            <Icon icon={icons.reTry} size={sizes.xl} />
          </button> :
          <div className={styles.calendarWrap}>
            <CalendarSwitch actualMonth={activeMonth} setMonth={setActiveMonth} />
            <CalendarList events={chunk(mapOnMonth(events), 7)} addEvent={addEvent} updateEvent={updateEvent} />
          </div>
      }
    </>
  );
};

export default Calendar;
