import React from 'react';
import cx from 'classnames';
import styles from './App.scss';
import history from './createHistory';
import Navigation from './components/Navigation/Navigation';
import ToDo from './components/ToDo/ToDo';
import AGB from './components/agb/agb';
import { Switch, Route } from 'react-router-dom';

import CalendarContainer from './containers/Calendar';
import Calendar from './components/Calendar/Calendar';

export const App = () => {
  const scrollHandler = () => {
    const container = window.pageXOffset / window.innerWidth;
    switch (container) {
      case 0:
        history.push({ hash: '/#first' });
        return;
      case 1:
        history.push({ hash: '/#second' });
        return;
      case 2:
        history.push({ hash: '/#third' });
        return;
      case 3:
        history.push({ hash: '/#fourth' });
        return;
      default:
        return;
    };
  };

  document.addEventListener('scroll', scrollHandler);

  return (
    <div>
      <Switch>
        <Route exact path="/agb">
          <AGB /> 
        </Route>
        <Route path="/">
          <div className={styles.App}>
            <Navigation />
            <div id="first" className={cx(styles.section, styles.first)}>
              <div className={styles.container}>
                <CalendarContainer>
                  <Calendar />
                </CalendarContainer>
              </div>
            </div>
            <div id="second" className={cx(styles.section, styles.second)}>
              <div className={styles.container}>
                <ToDo />
              </div>
            </div>
            <div id="third" className={cx(styles.section, styles.third)}>
              <div className={styles.container}>
                <h1>3rd section</h1>
              </div>
            </div>
            <div id="fourth" className={cx(styles.section, styles.fourth)}>
              <div className={styles.container}>
                <h1>4th section</h1>
              </div>
            </div>
          </div>
        </Route>
      </Switch>
    </div>
  );
}

export default App;
